.header-top {
  position: relative;
  text-align: center;
  padding: 6px 0px;
  background: -webkit-gradient(
    linear,
    right top,
    left top,
    from(#52c234),
    to(#0cb94e)
  );
  background: linear-gradient(to left, #52c234, #0cb94e);
}
.header-top p {
  font-size: 15px;
  letter-spacing: 0.3px;
  color: var(--white);
}
.header-top p a {
  font-size: 12px;
  font-weight: 500;
  padding: 0px 12px;
  border-radius: 3px;
  line-height: 24px;
  margin-left: 10px;
  margin-bottom: 2px;
  letter-spacing: 0.3px;
  text-transform: uppercase;
  background: var(--white);
  color: var(--primary);
  transition: all linear 0.3s;
  -webkit-transition: all linear 0.3s;
  -moz-transition: all linear 0.3s;
  -ms-transition: all linear 0.3s;
  -o-transition: all linear 0.3s;
}
.header-top p a:hover {
  color: var(--white);
  background: var(--primary);
}
.header-top button {
  position: absolute;
  top: 50%;
  right: 30px;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
}
.header-top button i {
  width: 25px;
  height: 25px;
  font-size: 12px;
  line-height: 24px;
  border-radius: 50%;
  text-align: center;
  display: inline-block;
  color: var(--white);
  background: var(--primary);
}

.brand_logo {
  text-align: center;
}
.brand_logo img {
  width: 100px;
}
@media (max-width: 575px) {
